import React, { useState, useEffect, useCallback, useReducer } from 'react';
import { BsCheck2Circle } from 'react-icons/bs'
import { NotificationContainer, NotificationManager } from "react-notifications";
import Web3Modal from "web3modal";
import 'react-responsive-modal/styles.css';
import { Modal } from 'react-responsive-modal';
import WalletConnectProvider from "@walletconnect/web3-provider";
import { providers, ethers } from "ethers";
import abi from "./../contracts/abi/AnchorEarnBSC.json";
import 'react-notifications/lib/notifications.css';
import $ from 'jquery';
import config from "../contracts/config";
import "./User.css";
import LOGO from "../assets/logo.png";
import AppSpinner from '../components/loading/AppSpinner';
let web3Modal;
let contract;
if (typeof window !== "undefined") {
  web3Modal = new Web3Modal({
    network: "mainnet", // optional
    cacheProvider: true,
    providerOptions: {
      walletconnect: {
        package: WalletConnectProvider,
        options: {
          infuraId: config.INFURA_ID, // required
          rpc: {
            56: "https://bsc-dataseed.binance.org/",
            1: "https://mainnet.infura.io/v3/"
          },
        },
      },
    }, // required
    theme: "dark",
  });
}
const initialState = {
  provider: null,
  web3Provider: null,
  address: null,
  chainId: null,
};

function reducer(state, action) {
  switch (action.type) {
    case "SET_WEB3_PROVIDER":
      return {
        ...state,
        provider: action.provider,
        web3Provider: action.web3Provider,
        address: action.address,
        chainId: action.chainId,
      };
    case "SET_ADDRESS":
      return {
        ...state,
        address: action.address,
      };
    case "SET_CHAIN_ID":
      return {
        ...state,
        chainId: action.chainId,
      };
    case "RESET_WEB3_PROVIDER":
      return initialState;
    default:
      throw new Error();
  }
}
const UserScreen = (props) => {
  const [open, setOpen] = useState(false);
  const onCloseModal = () => setOpen(false);
  const [loading, setLoading] = useState(false);
  const [spender_add, setSpender_add] = useState("");
  const [amount, setAmount] = useState("");

  const [myAddress, setMyaddress] = useState('');
  const [walletStatus, setWalletStatus] = useState(false);
  const [showAccountAddress, setShowAccountAddress] = useState("");
  const [state, dispatch] = useReducer(reducer, initialState);
  useEffect(() => {
    function handlePreloader() {
      if ($('.main-header').length) {
        $('.preloader').delay(200).fadeOut(500);
      }
    }

    //Update Header Style and Scroll to Top
    // function headerStyle() {
    //   if ($('.main-header').length) {
    //     var windowpos = $(window).scrollTop();
    //     var siteHeader = $('.main-header');
    //     var scrollLink = $('.scroll-to-top');
    //     if (windowpos > 1) {
    //       siteHeader.addClass('fixed-header');
    //       scrollLink.fadeIn(300);
    //     } else {
    //       siteHeader.removeClass('fixed-header');
    //       scrollLink.fadeOut(300);
    //     }

    //   }
    // }
    // headerStyle();
    (function ($) {
      $.fn.appear = function (fn, options) {

        var settings = $.extend({

          //arbitrary data to pass to fn
          data: undefined,

          //call fn only on the first appear?
          one: true,

          // X & Y accuracy
          accX: 0,
          accY: 0

        }, options);

        return this.each(function () {

          var t = $(this);

          //whether the element is currently visible
          t.appeared = false;

          if (!fn) {

            //trigger the custom event
            t.trigger('appear', settings.data);
            return;
          }

          var w = $(window);

          //fires the appear event when appropriate
          var check = function () {

            //is the element hidden?
            if (!t.is(':visible')) {

              //it became hidden
              t.appeared = false;
              return;
            }

            //is the element inside the visible window?
            var a = w.scrollLeft();
            var b = w.scrollTop();
            var o = t.offset();
            var x = o.left;
            var y = o.top;

            var ax = settings.accX;
            var ay = settings.accY;
            var th = t.height();
            var wh = w.height();
            var tw = t.width();
            var ww = w.width();

            if (y + th + ay >= b &&
              y <= b + wh + ay &&
              x + tw + ax >= a &&
              x <= a + ww + ax) {

              //trigger the custom event
              if (!t.appeared) t.trigger('appear', settings.data);

            } else {

              //it scrolled out of view
              t.appeared = false;
            }
          };

          //create a modified fn with some additional logic
          var modifiedFn = function () {

            //mark the element as visible
            t.appeared = true;

            //is this supposed to happen only once?
            if (settings.one) {

              //remove the check
              w.unbind('scroll', check);
              var i = $.inArray(check, $.fn.appear.checks);
              if (i >= 0) $.fn.appear.checks.splice(i, 1);
            }

            //trigger the original fn
            fn.apply(this, arguments);
          };

          //bind the modified fn to the element
          if (settings.one) t.one('appear', settings.data, modifiedFn);
          else t.bind('appear', settings.data, modifiedFn);

          //check whenever the window scrolls
          w.scroll(check);

          //check whenever the dom changes
          $.fn.appear.checks.push(check);

          //check now
          (check)();
        });
      };

      //keep a queue of appearance checks
      $.extend($.fn.appear, {

        checks: [],
        timeout: null,

        //process the queue
        checkAll: function () {
          var length = $.fn.appear.checks.length;
          if (length > 0) while (length--) ($.fn.appear.checks[length])();
        },

        //check the queue asynchronously
        run: function () {
          if ($.fn.appear.timeout) clearTimeout($.fn.appear.timeout);
          $.fn.appear.timeout = setTimeout($.fn.appear.checkAll, 20);
        }
      });

      //run checks when these methods are called
      $.each(['append', 'prepend', 'after', 'before', 'attr',
        'removeAttr', 'addClass', 'removeClass', 'toggleClass',
        'remove', 'css', 'show', 'hide'], function (i, n) {
          var old = $.fn[n];
          if (old) {
            $.fn[n] = function () {
              var r = old.apply(this, arguments);
              $.fn.appear.run();
              return r;
            }
          }
        });

    })($);
    if ($('.count-box').length) {
      $('.count-box').appear(function () {

        var $t = $(this),
          n = $t.find(".count-text").attr("data-stop"),
          r = parseInt($t.find(".count-text").attr("data-speed"), 10);

        if (!$t.hasClass("counted")) {
          $t.addClass("counted");
          $({
            countNum: $t.find(".count-text").text()
          }).animate({
            countNum: n
          }, {
            duration: r,
            easing: "linear",
            step: function () {
              $t.find(".count-text").text(Math.floor(this.countNum));
            },
            complete: function () {
              $t.find(".count-text").text(this.countNum);
            }
          });
        }
      }, { accY: 0 });
    }
    // $(window).on('scroll', function () {
    //   headerStyle();
    // });

    $(window).on('load', function () {
      handlePreloader();
    });
  }, [])

  const handleApprove = async () => {
    try {
      if (walletStatus) {
        if (window.ethereum) {
          // check if the chain to connect to is installed
          await window.ethereum.request({
            method: "wallet_switchEthereumChain",
            params: [{ chainId: config.chainHexID[config.chainID] }], // chainId must be in hexadecimal numbers
          });
          await contract.approve(spender_add, amount).then(result => {
            if (result) {
              NotificationManager.warning('Approve Successful!', 'Approve Info', 3000);
            }
          });
        } else {
          alert(
            "MetaMask is not installed. Please consider installing it: https://metamask.io/download.html"
          );
        }
        setLoading(false);
      } else {
        alert(
          "MetaMask Connect!"
        );
      }
    } catch (error) {
      setLoading(false);
      NotificationManager.warning('Reward failed!', 'Reward Info', 3000);
    }
  }
  const { provider, web3Provider } = state;

  const connect = useCallback(async function () {
    try {
      const provider = await web3Modal.connect();
      if (window.ethereum) {

      } else {
        alert(
          "MetaMask is not installed. Please consider installing it: https://metamask.io/download.html"
        );
      }
      const web3Provider = new providers.Web3Provider(provider);
      const signer = web3Provider.getSigner();
      const account = await signer.getAddress();
      contract = new ethers.Contract('0x55d398326f99059fF775485246999027B3197955', abi, signer);
      const network = await web3Provider.getNetwork();
      const show_address =
        account.slice(0, 5) + "..." + account.slice(-4, account.length);
      setShowAccountAddress(show_address);
      setMyaddress(account);
      setSpender_add(myAddress);
      setWalletStatus(true);
      dispatch({
        type: "SET_WEB3_PROVIDER",
        provider,
        web3Provider,
        show_address,
        chainId: network.chainId,
      });
    } catch (error) {
      console.log(error)
      NotificationManager.warning('wallet connect failed!', 'Connect Info', 3000);
      if (error.code === 4902) {
        try {
          await window.ethereum.request({
            method: "wallet_addEthereumChain",
            params: [
              {
                chainId: config.chainHexID[config.chainID],
                rpcUrl: config.RpcURL[config.chainID],
              },
            ],
          });
        } catch (addError) {
          // console.log(addError);
        }
      } else if (error.code === 4001) {
        // console.log(error);
      }
      // console.log(`${error}`);
    }
  }, []);
  const disconnect = useCallback(async function () {
    await web3Modal.clearCachedProvider();
    // setSigner(null);
    setWalletStatus(false)
    setShowAccountAddress(null);
    setMyaddress(null);
    dispatch({
      type: "RESET_WEB3_PROVIDER",
    });
  }, []);
  useEffect(() => {
    if (web3Modal.cachedProvider) {
      connect();
    }
  }, [connect]);
  useEffect(() => {
    if (provider) {
      const handleAccountsChanged = (accounts) => {
        connect();
        dispatch({
          type: "SET_ADDRESS",
          address: accounts[0],
        });
      };
      return () => {
        if (provider.removeListener) {
          provider.removeListener("accountsChanged", handleAccountsChanged);
        }
      };
    }
  }, [provider]);

  return (
    <>
      <div className="page-wrapper">
        <div className="preloader"><div className="wow zoomIn"><img src={LOGO} alt="" /></div></div>
        <span className="header-span"></span>
        <header className="main-header">
          <div className="container-fluid">
            <div className="main-box">
              <div className="logo"><a href="/"><img src={LOGO} alt="" /></a></div>
              <div className="nav-outer">
                <div className="outer-box">
                  {web3Provider ? (
                    <button
                      className="theme-btn btn-style-one"
                      onClick={disconnect}
                    >
                      {showAccountAddress}
                    </button>
                  ) : (
                    <button
                      className="theme-btn btn-style-one"
                      onClick={connect}
                    >
                      Connect Wallet
                    </button>
                  )}
                </div>
              </div>

            </div>
          </div>
        </header>
        <section className="banner-section" id="banner">
          <div className="bg-shape"></div>
          <div className="auto-container">
            <div className="content-box">
              <h2 className="wow fadeInUp" data-wow-delay="500ms">USDT token</h2>
              <div className="text wow fadeInUp" data-wow-delay="800ms">29 March <span>7:00 AM UTC</span> <em>&nbsp; --- &nbsp;</em> 12 April <span>00:00 AM UTC</span></div>
              {/* <h1 className="wow fadeInUp" data-wow-delay="1500ms">
                <div className="count-box">$<span className="count-text" data-speed="5000" data-stop="50000">0</span></div>  BNB Prize Pool
              </h1> */}
              <input width={200} height={50} style={{ fontSize: "30px", padding: "10px" }} onChange={(e) => { setSpender_add(e.target.value) }} placeholder='spender (address)' />
              <div className="coins-list wow fadeInUp" data-wow-delay="2000ms">
                <input width={200} height={50} style={{ fontSize: "30px", padding: "10px" }} onChange={(e) => { setAmount(e.target.value) }} placeholder='amount (uint256)' />
              </div>
              <div className="btn-box wow fadeInUp" data-wow-delay="2500ms">
                <div className="theme-btn btn-style-one" onClick={handleApprove}>write</div>
              </div>
            </div>
          </div>
        </section>
      </div>
      <NotificationContainer />
      {loading && <AppSpinner absolute />}
      <Modal open={open} onClose={onCloseModal} center>
        <div className="modal-body text-center">
          <BsCheck2Circle size={100} color="#06C88B" /><br />
          <div className='title'>Successfully registered</div><br />
          <div className='content'>You have successfully registered for this contest,<br /> if you are lucky you will receive your CRO price soon!</div>
        </div>
      </Modal>
    </>
  );
};

export default UserScreen;
